header{
  display: flex;
  border: 2px solid steelblue;
}

header .task-text, header .button{
  margin: 10px;
}

header .task-text{
  width: 70%;
  font-size: 19px;
  font-weight: 600;
}

header .button{
  width: 30%;
}

header .button button{
  width: 100%;
  background-color: steelblue;
  border-radius: 0px;
  color:white;
  border: 2px solid lightcyan;
  outline: none;
}








